/* Estilos para el popup */
/* Fondo del popup */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7); 
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Card del popup */
.popup-content {
  width: 100%;
  max-width: 450px; 
  height: 80%;
  perspective: 1000px;
  border-radius: 10px; 
  border: 0px solid rgba(0, 255, 255, 0); 
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.7); 
  padding: 40px;
  overflow: hidden; 
}

.popup-content p {
 font-size: 15px;
}

/* Efecto de flip */
.popup-inner {
  margin-top: 200px;
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s ease;
  transform-style: preserve-3d;
}

.popup-inner.flipped {
  transform: rotateY(180deg);
}

/* Caras de la card */
.popup-front, .popup-back {
  position: absolute;
  backface-visibility: hidden;
  width: 100%;
  height: 90%;
  top: -200px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  border-radius: 0px; 
  box-sizing: border-box; 
}

