  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.edit-profile-container input,
.edit-profile-container textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 14px;
}

.edit-profile-container button {
  background-color: #000000;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.edit-profile-container button:hover {
  background-color: #000000;
  color: #ffffff;
  box-shadow: 0 5px 10px rgba(243, 243, 243, 0.938);
  transform: scale(1.05); 
}
.star-profile {
  position: absolute;
  background-color: white; 
  border-radius: 50%;
  animation: blink 1.5s infinite alternate; 
}

@keyframes blink {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.snow-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden; 
  pointer-events: none; 
}

.snowflake {
  position: absolute;
  background-color: white;
  border-radius: 50%;
  opacity: 0.8;
  animation: fall linear infinite; 
}

/* Animación de caída de la nieve */
@keyframes fall {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(100vh); 
  }
}

.snowflake {
  animation-duration: calc(2s + 3s * var(--random));
  animation-delay: calc(0s + 3s * var(--random)); 
}

/* Carrusel de perfil*/

.avatar-options {
  display: flex;
  gap: 15px;
  overflow: hidden;
  width: 150px;
  height: 80px;
}

.avatar-selection {
  display: flex;
